.overlay {
  text-align: center;
}

#modal {
  position: fixed;
  width: 65%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0px 0px 4px 4px rgba(255, 0, 0, 0.8);
  border-radius: 1rem;
  display: block;
}

#backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: block;
}

.hidden {
  transform: scaleY(0);
  transition: transform 400ms ease 0ms;
}
